import { TextField } from "@mui/material";
import { useField } from "formik";

const DateTimePicker = ({ name, ...otherProps }) => {
  const [field, meta] = useField(name);

  const configDateTimePicker = {
    ...field,
    ...otherProps,
    fullWidth: true,
    variant: "outlined",
    type: "date",
    InputLabelProps: {
      shrink: true,
    },
  };

  if (meta && meta.touched && meta.error) {
    configDateTimePicker.error = true;
    configDateTimePicker.helperText = meta.error;
  }
  if (meta && meta.value && meta.touched) {
    const dateTime = meta.value.split("-")[0];
    const nowTime = new Date().toISOString().split("-")[0];
    const currentAge = nowTime - dateTime * 1;
    if (currentAge < 17) {
      configDateTimePicker.error = true;
      configDateTimePicker.helperText = "At least 18 yesrs old";
    }
  }

  return <TextField {...configDateTimePicker} />;
};

export default DateTimePicker;
